.SearchComponent {
  padding-bottom: 10px;
  min-height: 52px;
}
.SearchComponent .ant-form-item-control,
.SearchComponent .ant-form-item-label {
  line-height: 32px;
  height: 32px;
}
.SearchComponent .ant-form-item {
  margin-bottom: 20px;
  display: flex;
}
.SearchComponent .ant-form-item-control-wrapper {
  flex: 1 1;
}
.SearchComponent .isModal .ant-form-item {
  display: block;
}
.SearchComponent .isMobile .ant-form-item {
  display: block;
}
@media (min-width: 768px) {
  .SearchComponent .ant-form-item {
    display: flex;
  }
}
@media (max-width: 768px) {
  .SearchComponent .ant-form-item-label {
    width: auto;
  }
  .SearchComponent .ant-form-item-control-wrapper {
    width: auto;
  }
}
